/* GENERAL */
/* -------------------------------- */
* {
  margin: 0;
  padding: 0;
}

p {
  font-family: "Nunito", sans-serif;
}

h1 {
  font-family: "Abril Fatface", cursive;
}

@import url("https://fonts.googleapis.com/css?family=Abril+Fatface&display=swap");
@import url("https://fonts.googleapis.com/css?family=Nunito:300,400,700&display=swap");
/* -------------------------------- */

/* NAVBAR */
/* -------------------------------- */
.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center; /* Change to space-between when re-adding navitems */
  align-items: center;
  background-color: #ebebeb;
  padding: 22px 100px;
}

.flex-right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 400px;
}

.logo {
  height: 85px;
}

.show-on-mobile {
  display: none;
}

@media only screen and (max-width: 1024px) {
  .wrapper {
    padding: 18px 50px;
  }

  .logo {
    height: 60px;
  }

  .show-on-mobile {
    display: block;
  }
}
/* -------------------------------- */

/* HOME GRID */
/* -------------------------------- */
.grid-img-left {
  display: grid;
  grid-template-columns: 100px 450px 80px auto 100px;
  grid-template-rows: 100px 300px 100px;
}

.grid-img-right {
  display: grid;
  grid-template-columns: 100px auto 80px 450px 100px;
  grid-template-rows: 100px 300px 100px;
  background-color: #fbfbfb;
}

.img-left {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
}

.txt-right {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 2;
  grid-row-end: 3;
  align-self: center;
}

.img-right {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 2;
  grid-row-end: 3;
}

.txt-left {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
  align-self: center;
}

.img-left img,
.img-right img {
  object-fit: cover;
  object-position: 0 30%;
  height: 300px;
  width: 450px;
}

@media only screen and (max-width: 1024px) {
  .grid-img-left,
  .grid-img-right {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .img-right {
    order: 0;
  }

  .img-right img,
  .img-left img {
    height: 200px;
    width: 300px;
  }

  .text-left {
    order: 1;
  }

  .img-left,
  .img-right {
    margin: 75px 50px 0 50px;
  }

  .txt-right,
  .txt-left {
    margin: 50px;
    text-align: center;
  }
}
/* -------------------------------- */

/* HOME MISC */
/* -------------------------------- */
.title {
  color: #4ead33;
  letter-spacing: 2px;
  font-size: 40px;
  font-weight: 100;
  margin-bottom: 16px;
}

.txt {
  text-align: justify;
}

@media only screen and (max-width: 1024px) {
  .title {
    font-size: 28px;
  }
}
/* -------------------------------- */

/* FOOTER GENERAL */
/* -------------------------------- */
.footer_wrapper {
  background-color: #373736;
  color: #f3f3f3;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 390px;
}

.info {
  justify-self: flex-end;
  margin: 55px 100px 55px 0;
}

@media only screen and (max-width: 1024px) {
  .hide-on-mobile {
    display: none;
  }

  .info {
    margin: 55px auto;
  }
}

/* -------------------------------- */

/* FOOTER FORM */
/* -------------------------------- */
.form {
  margin: 55px 0 55px 100px;
  display: grid;
  grid-template-columns: 250px 20px 250px;
  grid-template-rows: 50px 20px 150px 20px 40px;
}

.line-input {
  font-family: "Nunito", sans-serif;
  font-size: 18px;
  border-radius: 8px;
  border: none;
  padding: 0 23px;
}

.area-input {
  font-family: "Nunito", sans-serif;
  font-size: 18px;
  resize: none;
  border-radius: 8px;
  border: none;
  padding: 13px 23px;
}

#name-input {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
}

#email-input {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
}

#message-input {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 3;
  grid-row-end: 4;
}

.button-form {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 5;
  grid-row-end: 6;

  border-radius: 8px;
  border: none;
  width: 200px;
  justify-self: center;

  font-family: "Nunito", sans-serif;
  font-size: 18px;

  background-color: #4ead33;
  color: #f3f3f3;
}

.message {
  grid-column-start: 1;
  grid-column-end: 4;
  grid-row-start: 5;
  grid-row-end: 6;

  justify-self: center;
}

.sr-footer {
  text-align: center;
  padding: 8px;
  font-size: 12px;
}
/* -------------------------------- */

/* FOOTER INFO */
/* -------------------------------- */
.info {
  display: grid;
  grid-template-columns: auto 35px 1px 35px 80px;
  grid-template-rows: 25px 230px 25px;
}

.info-left {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;

  font-size: 18px;
  align-self: center;
}

@media only screen and (max-width: 1024px) {
  .info-left {
    font-size: 14px;
  }
}

.info-left .part-1,
.info-left .part-2 {
  margin-bottom: 20px;
}

.info-left .part-1 p,
.info-left .part-2 p,
.info-left .part-3 p:not(:last-child) {
  margin-bottom: 5px;
}

.socials {
  grid-column-start: 5;
  grid-column-end: 6;
  grid-row-start: 2;
  grid-row-end: 3;
  height: 30px;
  width: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: center;
}

@media only screen and (max-width: 1024px) {
  .info {
    display: grid;
    grid-template-columns: auto 35px 1px 35px 50px;
    grid-template-rows: 25px 230px 25px;
  }

  .socials {
    flex-direction: column;
    height: 100px;
    align-items: center;
  }
}

.socials a {
  color: #ebebeb;
}

.socials #facebook:hover {
  color: #3b5999;
}

.socials #instagram:hover {
  color: #e4405f;
}

.divider {
  background-color: #f3f3f3;

  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 4;
}
/* -------------------------------- */
